new Vue({
	el: '#datetimepicker',
	name: 'DateTimePicker',
	data: {
		updateCounter: 0,
		times: [],
		selectedDate: null,
		selectedTime: null,
	},
	methods: {
		setTime(time) {
			this.selectedTime = time;
		},
		datepickerInit() {
			const self = this;

			new Pikaday({
				field: document.getElementById('datepicker'),
				container: document.querySelector('.datepicker-container'),
				bound: false,
				disableDayFn: self.isDayDisabled,
				onSelect: self.onDatepickerSelect,
			});

			// Hack
			this.$nextTick(() => {
				this.updateCounter++;
			});
		},
		isDayDisabled(day) {
			// Check every day of month availability
			// return true - disabled, false - available
		},
		onDatepickerSelect(day) {
			this.selectedDate = dayjs(day).format('DD.MM.YYYY');
		},
		getTimes() {
			this.times = ['10:40', '11:40', '15:20', '16:50', '18:00'];
		},
	},
	computed: {
		resultDateTime() {
			if (this.selectedDate && this.selectedTime) {
				return `Moscow Time (${this.selectedDate} ${this.selectedTime})`;
			} else {
				return null;
			}
		},
	},
	mounted() {
		this.getTimes();
		this.datepickerInit();
	},
});
