const $popupButtons = document.querySelectorAll('.js-popup-btn');
const $popupOuter = document.querySelector('.popup-outer');
const $popup = document.querySelector('.popup--form');
const $popupResult = document.querySelector('.popup--result');
const $popupCloseButtons = document.querySelectorAll('.popup .close-button');

window.showPopup = () => {
	$popupOuter.classList.add('showed');
	$popup.classList.add('showed');
};

window.showResultPopup = () => {
	$popupResult.classList.add('showed');
};

window.closeAllPopups = () => {
	$popupOuter.classList.remove('showed');
	$popup.classList.remove('showed');
	$popupResult.classList.remove('showed');
};

window.closePopup = () => {
	$popup.classList.remove('showed');
};

$popupButtons.forEach((popupButton) => {
	popupButton.addEventListener('click', (e) => {
		e.preventDefault();

		window.showPopup();
	});
});

$popupOuter.addEventListener('click', (e) => {
	e.stopPropagation();

	if (e.target.classList.contains('popup-outer')) {
		window.closeAllPopups();
	}
});

$popupCloseButtons.forEach((item) => {
	item.addEventListener('click', (e) => {
		e.preventDefault();

		window.closeAllPopups();
	});
});
