const goToLinks = document.querySelectorAll('.js-go-to');

function goTo(elQuery) {
	const targetEl = document.querySelector(elQuery);

	if (targetEl) {
		document.querySelector(elQuery).scrollIntoView({ behavior: 'smooth' });
	}
}

goToLinks.forEach((link) => {
	link.addEventListener('click', (e) => {
		e.preventDefault();

		const targetId = link.getAttribute('href');

		goTo(targetId);
	});
});
